<template>
  <div class="management">
    <div class="search">
      <form action="/">
        <van-search v-model="value" show-action action-text="搜索" @cancel="onSearch" placeholder="搜索活动名称" background="#fff" @search="onSearch" @blur="blur" />
      </form>
    </div>
    <div class="mid">
      <div class="list mescroll" id="list">
        <div id="listData">
          <div class="item" v-for="(item,index) in list" :key="index">
            <van-swipe-cell>
              <div class="flex">
                <div class="left" @click="goDetail(item.acode)">
                  <img :src="item.aimg?$utils.dealImg(item.aimg):require('@/assets/img/default/defaultH.png')" alt />
                </div>
                <div class="flex-mid" @click="goDetail(item.acode)">
                  <p class="title">{{item.aname}}</p>
                  <!-- <div class="bottom">
                    <div class="text1">{{item.asalenum}}人开通</div>
                    <div class="text2">{{item.aoutPrice?'￥'+item.aoutPrice.toFixed(2):'免费'}}</div>
                  </div> -->
                </div>
                <div class="right">
                  <div @click="toTeam(item.acode)">活动用户》</div>
                  <div @click="toEarnings(item.acode)">我的收益》</div>
                </div>
              </div>
              <template slot="right">
                <van-button square type="danger" text="编辑" @click="edit(item.acode)" />
              </template>
            </van-swipe-cell>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom" @click="add">新增活动</div>
  </div>
</template>
<script>
import { SwipeCell, Search, Button, Icon } from "vant";
import { getActivityManagementList } from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [SwipeCell.name]: SwipeCell,
    [Button.name]: Button,
    [Search.name]: Search,
    [Icon.name]: Icon
  },
  data() {
    return {
      active: 0,
      value: "",
      list: []
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    onSearch() {
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    onClick() {
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    toTeam(acode) {
      this.$router.push({
        name: "activityTeam",
        query: {
          acode: acode,
          type: 2
        }
      });
    },
    goDetail(acode) {
      this.$router.push({
        name: "shareActivity",
        query: {
          id: acode
        }
      });
    },
    edit(acode) {
      this.$router.push({
        name: "addActivity",
        query: {
          acode: acode
        }
      });
    },
    toEarnings(acode) {
      this.$router.push({
        name: "earnings",
        query: {
          acode: acode,
          type: "2"
        }
      });
    },
    add() {
      this.$router.push({
        name: "addActivity"
      });
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        pageNum: pageNum,
        pageSize: pageSize,
        aname: this.value
      };
      getActivityManagementList(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            $this.$toast.fail(res.errorMsg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据"
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true // 是否开启懒加载,默认false
          }
        }
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          if (page.num == 1) $this.list = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this.list.push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.count
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    document.title = "活动管理";
    this.startMescroll();
  }
};
</script>
<style lang="scss" scoped>
.management {
  .mid {
    background: #fff;
    height: calc(100vh - 54px - 50px);
    .list {
      padding: 10px;
      .item {
        margin-bottom: 10px;
        padding: 5px 0;
        background: #fff;
        border-bottom: 1pt solid #efefef;
        width: calc(100vw - 20px);

        .flex {
          display: flex;
          align-items: center;
          .left {
            img {
              width: 120px;
              margin-right: 5px;
            }
          }
          .flex-mid {
            position: relative;
            flex: 2;
            margin-right: 5px;
            .title {
              font-size: 13px;
              line-height: 20px;
              max-height: 50px;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            }
            .bottom {
              position: absolute;
              bottom: 0;
              left: 0;
              display: flex;
              width: 100%;
              line-height: 30px;
              .text1 {
                color: #999;
                font-size: 13px;
                flex: 2;
              }
              .text2 {
                flex: 1;
                text-align: right;
                color: #9e1435;
                font-size: 13px;
              }
            }
          }
          .right {
            font-size: 14px;
            line-height: 25px;
            color: #f56873;
          }
        }
        .item-bottom {
          text-align: center;
          overflow: hidden;
          padding: 15px 0;
          .btn {
            width: 80px;
            color: #666;
            border: 1pt solid #e5e5e5;
            float: right;
            height: 30px;
            line-height: 30px;
            border-radius: 5px;
            font-size: 13px;
            margin-left: 5px;
          }
        }
      }
    }
  }
  .bottom {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100vw;
    height: 50px;
    line-height: 50px;
    background: $base-color;
    color: #fff;
    text-align: center;
  }
}
</style>
<style lang="scss">
.management {
  .van-button {
    height: 100%;
  }
}
</style>
